import { zodResolver } from "@hookform/resolvers/zod";
import { Box, Button } from "@mui/material";
import { CONSTANTS, DASHBOARD_PAGE } from "app/constants/TranslationNamespaces";
import CustomDatePickerField from "app/shared-components/custom-date-picker-field/CustomDatePickerField";
import { useForm } from "react-hook-form";
import { useTranslation } from "react-i18next";
import { z } from "zod";
import { motion } from "framer-motion";
import ApprovedRejectedRatioWidget from "./widgets/approved-rejected-ratio-widget/ApprovedRejectedRatioWidget";
import UserAdoptionRateWidget from "./widgets/user-adoption-rate-widget/UserAdoptionRateWidget";
import WaitingListWidget from "./widgets/waiting-list-widget/WaitingListWidget";
import AvgTimeToRespondWidget from "./widgets/avg-time-to-respond-widget/AvgTimeToRespondWidget";
import BookingByPhaseWidget from "./widgets/booking-by-phase-widget/BookingByPhaseWidget";
import FuseSvgIcon from "@fuse/core/FuseSvgIcon";
import { useGetDashboardKpisQuery } from "./DashboardApi";
import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { showLoading } from "app/shared-components/custom-loading/CustomLoadingSlice";
import { IDashboardEssentials, IDashboardInput } from "./DashboardModel";
import { showMessage } from "@fuse/core/FuseMessage/fuseMessageSlice";
import { snackbarDefaults } from "app/constants/SnackbarDefaults";

type DashboardContentProps = {};

const DashboardContent = (props: DashboardContentProps) => {
	const defaultDashboardContentValues = {
		approvedRejectionRatio: 0,
		userAdoptionRate: 0,
		waitingListCount: 0,
		avgTimeToRespond: 0
	};
	const [dashboardContent, setDashboardContent] = useState<IDashboardEssentials>(defaultDashboardContentValues);
	const [fetchArgs, setFetchArgs] = useState<IDashboardInput>({ startDate: new Date(), endDate: new Date() });
	const getSlotReleaseSchedule = useGetDashboardKpisQuery(fetchArgs, { refetchOnMountOrArgChange: true });
	const dispatch = useDispatch();

	const container = {
		show: {
			transition: {
				staggerChildren: 0.1
			}
		}
	};

	const item = {
		hidden: { opacity: 0, y: 20 },
		show: { opacity: 1, y: 0 }
	};

	const constantTranslation = useTranslation(CONSTANTS);
	const dashboardTranslation = useTranslation(DASHBOARD_PAGE);

	const defaultValues: IDashboardInput = {
		startDate: new Date(),
		endDate: new Date()
	};

	const schema = z.object({
		startDate: z.date(),
		endDate: z.date()
	});

	const methods = useForm({
		mode: "onChange",
		defaultValues: defaultValues,
		resolver: zodResolver(schema)
	});

	const { control, formState } = methods;
	const { errors } = formState;

	useEffect(() => {
		const { data, isLoading, isSuccess, isError, error, isFetching } = getSlotReleaseSchedule;

		dispatch(showLoading(isFetching));
		dispatch(showLoading(isLoading));

		if (isSuccess && data) {
			const { approvedRejectionRatio, avgTimeToRespond, userAdoptionRate, waitingListCount } = data;
			setDashboardContent((state) => ({ ...state, approvedRejectionRatio, avgTimeToRespond, userAdoptionRate, waitingListCount }));
		}

		if (isError && error) {
			dispatch(showMessage(snackbarDefaults(`${constantTranslation.t("ERROR_LOADING_DATA")} ${dashboardTranslation.t("TITLE")}`, "error")));
		}

		return () => setDashboardContent(defaultDashboardContentValues);
	}, [getSlotReleaseSchedule]);

	return (
		<Box sx={{ width: "100%" }}>
			<motion.div
				variants={container}
				className="flex flex-col sm:flex-row px-32"
			>
				<motion.div
					className="w-full sm:w-1/2 sm:mr-8"
					variants={item}
					initial="hidden"
					animate="show"
				>
					<CustomDatePickerField
						control={control}
						name="startDate"
						label={constantTranslation.t("START_DATE") + "*"}
						errors={errors}
						slotProps={{
							textField: { InputLabelProps: { shrink: true } }
						}}
						dialog="true"
						onInputChange={(value) => setFetchArgs((state) => ({ ...state, startDate: value }))}
					/>
				</motion.div>
				<motion.div
					className="w-full sm:w-1/2 sm:ml-8"
					variants={item}
					initial="hidden"
					animate="show"
				>
					<CustomDatePickerField
						control={control}
						name="endDate"
						label={constantTranslation.t("END_DATE") + "*"}
						errors={errors}
						slotProps={{
							textField: { InputLabelProps: { shrink: true } }
						}}
						dialog="true"
						onInputChange={(value) => setFetchArgs((state) => ({ ...state, endDate: value }))}
					/>
				</motion.div>
			</motion.div>
			<motion.div
				variants={container}
				className="flex flex-col sm:flex-row px-32 mb-24 gap-24 w-full sm:w-1/2 lg:w-1/3"
			>
				<motion.div
					className="w-full md:w-1/2"
					variants={item}
					initial="hidden"
					animate="show"
				>
					<Button
						className="rounded-lg w-full"
						color="secondary"
						variant="contained"
					>
						<div className="flex flex-row">
							<FuseSvgIcon
								className="text-48 mr-10"
								size={24}
							>
								heroicons-outline:document
							</FuseSvgIcon>
							<span className="whitespace-nowrap">Export PDF</span>
						</div>
					</Button>
				</motion.div>
				<motion.div
					className="w-full md:w-1/2"
					variants={item}
					initial="hidden"
					animate="show"
				>
					<Button
						className="rounded-lg w-full"
						color="secondary"
						variant="contained"
					>
						<div className="flex flex-row">
							<FuseSvgIcon
								className="text-48 mr-10"
								size={24}
							>
								heroicons-outline:download
							</FuseSvgIcon>
						</div>
						<span className="whitespace-nowrap">CSV</span>
					</Button>
				</motion.div>
			</motion.div>
			<motion.div
				className="py-12 px-32"
				variants={item}
				initial="hidden"
				animate="show"
			>
				<BookingByPhaseWidget />
			</motion.div>
			<motion.div
				variants={container}
				initial="hidden"
				animate="show"
				className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-2 xl:grid-cols-4 gap-24 w-full min-w-0 py-24 px-32"
			>
				<motion.div variants={item}>
					<ApprovedRejectedRatioWidget approvedRejectionRatio={dashboardContent.approvedRejectionRatio} />
				</motion.div>
				<motion.div variants={item}>
					<UserAdoptionRateWidget userAdoptionRate={dashboardContent.userAdoptionRate} />
				</motion.div>
				<motion.div variants={item}>
					<WaitingListWidget waitingListCount={dashboardContent.waitingListCount} />
				</motion.div>
				<motion.div variants={item}>
					<AvgTimeToRespondWidget avgTimeToRespond={dashboardContent.avgTimeToRespond} />
				</motion.div>
			</motion.div>
		</Box>
	);
};

export default DashboardContent;
