import FusePageSimple from "@fuse/core/FusePageSimple";
import PageHeader from "app/shared-components/page-header/PageHeader";
import DefaultRootStyle from "app/constants/DefaultRootStyle";
import { useTranslation } from "react-i18next";
import { styled } from "@mui/material/styles";
import { CONSTANTS } from "app/constants/TranslationNamespaces";
import { useAppSelector } from "app/store/hooks";
import { selectUser } from "src/app/auth/user/store/userSlice";
import DashboardContent from "./DashboardContent";

const Root = styled(FusePageSimple)(({ theme }) => ({
	...DefaultRootStyle(theme)
}));

const Dashboard = () => {
	const user = useAppSelector(selectUser);
	const constantTranslation = useTranslation(CONSTANTS);

	return (
		<Root
			header={<PageHeader title={`${constantTranslation.t("WELCOME")} ${user.data.displayName}`} />}
			scroll="page"
			content={<DashboardContent />}
		/>
	);
};

export default Dashboard;
