import CustomWidget from "app/shared-components/custom-widget/CustomWidget";
import { useTranslation } from "react-i18next";
import { DASHBOARD_PAGE } from "app/constants/TranslationNamespaces";

type WaitingListWidgetProps = {
	waitingListCount: number;
};

const WaitingListWidget = (props: WaitingListWidgetProps) => {
	const { waitingListCount } = props;
	const dashboardTranslation = useTranslation(DASHBOARD_PAGE);
	return (
		<CustomWidget
			title={dashboardTranslation.t("WAITING_LIST")}
			color="pink"
		>
			{waitingListCount}
		</CustomWidget>
	);
};

export default WaitingListWidget;
