import { DASHBOARD_PAGE } from "app/constants/TranslationNamespaces";
import CustomWidget from "app/shared-components/custom-widget/CustomWidget";
import { useTranslation } from "react-i18next";
import Chart from "react-apexcharts";
import { useEffect, useState } from "react";
import { ApexOptions } from "apexcharts";
import { motion } from "framer-motion";
import useWindowDimensions from "app/hooks/useWindowDimensions";

type BookingByPhaseWidgetProps = {};

const BookingByPhaseWidget = (props: BookingByPhaseWidgetProps) => {
	const { width } = useWindowDimensions();
	const [chartWidth, setChartWidth] = useState(0);

	useEffect(() => {
		if (width) {
			if (width - 512 > 512 && width < 1950) {
				setChartWidth(width - 512);
			} else if (width > 1950) {
				setChartWidth(1400);
			} else if (width - 512 < 512) {
				setChartWidth(width - 100);
			}
		}
	}, [width]);

	const dashboardTranslations = useTranslation(DASHBOARD_PAGE);
	const [apexOptions, setApexOptions] = useState<ApexOptions>({
		chart: {
			toolbar: {
				show: false
			},
			selection: {
				enabled: false
			}
		},
		dataLabels: {
			enabled: false
		},
		xaxis: {
			categories: ["Phase 1", "Phase 2", "Phase 3", "Phase 4", "Phase 5", "Phase 6", "Phase 7", "Phase 8"],
			stepSize: 5
		},
		fill: {
			colors: ["#803AED"]
		},
		plotOptions: {
			bar: {
				columnWidth: "50%"
			}
		}
	});
	const [apexSeries, setApexSeries] = useState<ApexAxisChartSeries>([
		{
			name: "Bookings",
			data: [30, 40, 25, 50, 49, 21, 70, 51]
		}
	]);

	return (
		<CustomWidget
			chart={true}
			title={dashboardTranslations.t("BOOKING_BY_PHASE")}
		>
			<motion.div
				initial={{ opacity: 0, y: 10 }}
				animate={{ opacity: 1, y: 0, transition: { delay: 0.25 } }}
			>
				<Chart
					options={apexOptions}
					series={apexSeries}
					type="bar"
					width={chartWidth}
					height={250}
				/>
			</motion.div>
		</CustomWidget>
	);
};

export default BookingByPhaseWidget;
