import { Typography } from "@mui/material";
import { Paper } from "@mui/material";
import { ReactNode } from "react";

type CustomWidgetProps = {
	children?: ReactNode;
	title?: string;
	color?: string;
	chart?: boolean;
};

const CustomWidget = (props: CustomWidgetProps) => {
	const { children, title, color, chart } = props;
	const colors = {
		purple: "text-purple-600",
		orange: "text-orange-500",
		pink: "text-pink-500",
		lime: "text-lime-500"
	};

	return (
		<Paper className="flex flex-col flex-auto shadow rounded-2xl overflow-hidden justify-center items-center">
			<Typography className="p-24 text-lg font-medium tracking-tight leading-6 w-full">{title}</Typography>
			<div className="text-center mb-24">{chart ? children : <Typography className={`text-6xl sm:text-7xl md:text-8xl font-bold tracking-tight leading-none ${colors[color]}`}>{children}</Typography>}</div>
		</Paper>
	);
};

export default CustomWidget;
