import { apiService as api } from "app/store/apiService";
import { IDashboardInput, IDashboardEssentials } from "./DashboardModel";
import moment from "moment";

export const addTagTypes = [] as const;

const DashboardApi = api.enhanceEndpoints({ addTagTypes }).injectEndpoints({
	endpoints: (build) => ({
		getDashboardKpis: build.query<GetDashboardResponse, GetDashboardApiArg>({
			query: ({ startDate, endDate }) => {
				let params = "?";

				const formatDate = (date: Date) => moment(date).format("MM/DD/YYYY");

				if (startDate) {
					let formatedDate = formatDate(startDate);
					params += `startDate=${formatedDate}&`;
				}
				if (endDate) {
					let formatedDate = formatDate(endDate);
					params += `endDate=${formatedDate}`;
				}

				return {
					url: `/dashboard/getdashboardkpis/${params}`
				};
			}
		})
	})
});

export default DashboardApi;

export type GetDashboardApiArg = IDashboardInput;
export type GetDashboardResponse = IDashboardEssentials;

export const { useGetDashboardKpisQuery } = DashboardApi;

export type DashboardApiType = {
	[DashboardApi.reducerPath]: ReturnType<typeof DashboardApi.reducer>;
};
